import React, { useState } from 'react';
import Button from 'react-bootstrap/Button';
import Modal from 'react-bootstrap/Modal';
import ListGroup from 'react-bootstrap/ListGroup';

function Restoperation({operate}) {
    console.log(operate);

    const [show, setShow] = useState(false);

    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);
  return (
    <div>
         <>
         
      <Button  variant="primary" onClick={handleShow}>
        operating hours
      </Button>

      <Modal  show={show} onHide={handleClose}>
        <Modal.Header  closeButton>
          <Modal.Title style={{'color':'black'}}>operating hours</Modal.Title>
        </Modal.Header>
        <Modal.Body>
        <ListGroup>
      <ListGroup.Item style={{'background-color':'orange'}}>Monday:  {operate.Monday}</ListGroup.Item>
      <ListGroup.Item style={{'background-color':'orange'}}>Tuesday:{operate.Tuesday}</ListGroup.Item>
      <ListGroup.Item style={{'background-color':'orange'}}>Wednesday:{operate.Wednesday}</ListGroup.Item>
      <ListGroup.Item style={{'background-color':'orange'}}>Thursday:{operate.Thursday}</ListGroup.Item>
      <ListGroup.Item style={{'background-color':'orange'}}>Friday:{operate.Friday}</ListGroup.Item>
    </ListGroup>
        </Modal.Body>
        
      </Modal>
    </>
         
    </div>
         
  )
}

export default Restoperation