import React,{useState,useEffect} from 'react'
import { useParams } from 'react-router-dom'
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import { Image } from 'react-bootstrap';
import Restoperation from './Restoperation';
import Restreviews from './Restreviews';


function Viewrestaurant() {
  const [index, setIndex] = useState(0);
  const handleSelect = (selectedIndex, e) => {
    setIndex(selectedIndex);
  };
    const params=useParams();
    console.log(params.id);
    //to hold values
    const[allRestaurants,setRestaurants]=useState([])

    //function to call API to get all information from restaurant.json
    const getRestaurants=async()=>{
        await fetch('/restaurants.json')
        .then((data)=>{
            data.json()
            .then((result)=>{
              //  console.log(result);
              setRestaurants(result.restaurants)
            })
           // console.log(data);
        })
       
    }
    useEffect(()=>{
        getRestaurants()
    },[])
   console.log(allRestaurants);

const viewrest=allRestaurants.find(item=>item.id==params.id)
console.log("viewresssss",viewrest)

    
  return (
    <>
{
      viewrest?(


       
    <Row>


        <Col>
        <Image src={viewrest.photograph}fluid/>
        </Col>
        <Col>
        <p>ID:{viewrest.id}</p>
        <h3>Name:{viewrest.name}</h3>
        <h3>neighborhood:{viewrest.neighborhood}</h3>
        <h3>Address:{viewrest.address}</h3>
        <h3>cuisine_type:{viewrest.cuisine_type}</h3>
        <Restoperation operate={viewrest.operating_hours}/>
        <Restreviews reviews={viewrest.reviews}/>



        </Col>
        </Row>
    
      

        ):'null'
    }
    </>
    
    
  )
}

export default Viewrestaurant