import {R_SUCCESS,R_FAILIURE} from '../constants/restaurantConstants';


export const restaurantlistReducer=(state={restaurantlist:[]},action)=>{
    switch(action.type){
        case R_SUCCESS:
        return{
            restaurantlist:action.payload
        }
        case R_FAILIURE:
            return{
                restaurantlist:action.error
            }
            default:
                return state
    }
}