import{combineReducers,applyMiddleware,createStore} from 'redux';
import thunk from 'redux-thunk'
import { restaurantlistReducer } from './Reducer/restaurantReducer';
//to generate moiddleware



const reducer=combineReducers({//2 step
    restaurantReducer:restaurantlistReducer

})
const middleware=[thunk]

const store=createStore(reducer,applyMiddleware(...middleware))//1 step

export default store